import * as PIXI from 'pixi.js'
import { objectBase } from '../../engine/objectHandlers/objectBase';
import { objectFunctions } from '../../engine/objectHandlers/objectFunctions';
import { roomEvent } from '../../engine/roomEvent/roomEvent';
import { r1_scene_lake1 } from '../../scenes/region1/r1_scene_lake1';
import { scene_home } from '../../scenes/scene_home';
import { player } from '../player';
import { textPromptBox } from '../textPromptBox';


export class handleRoomStartString extends objectBase{
    switch: boolean = false;
    static objectName = "handleRoomStartString";


    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, handleRoomStartString.objectName);
        
    }

    afterInit(l: objectFunctions){
        let args = l.getRoomStartString();
        args.forEach(arg => {
            if(arg == "dream_with_dad_1"){
                let p = new player(1187, 560, "");
                l.addObjectLayerName(p, "Layer 1");
                l.flags().add("freezeObjects");

                let prompt = new textPromptBox(0, 0, JSON.stringify([
                        "Hej och välkommen till denna demo! Jag kommer att gå igenom kontrollerna. Tryck enter för att gå vidare.",
                        "Använd [WASD] för att gå med din karaktär, [W] för att hoppa. Håll in [CONTROL] för att springa.",
                        "Håll ner [S] och tryck [J] för att gå ner till lägre mark. tryck [W] om du befinner dig på en grön rektangel för att gå upp högre mark.",
                        "Använd [K] för att attackera. Du Kan kombinera [K] med olika rörelser för att få olika attacker.\nProva attackera den rosa lådan."]));
                prompt.setPromptCallback((l: objectFunctions) => {
                    l.flags().remove("freezeObjects");
                    l.flags().add("day");
                    l.goToRoom(scene_home, []);
                });
                l.addObjectLayerName(prompt, "GUILayer");
            }    
        });
    }



}